@import url(https://fonts.googleapis.com/css2?family=Fraunces:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yusei+Magic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.tsparticles-canvas-el{
  position: fixed;
  height: 1000px;
  width: 100vh;
  z-index: 1;
}
:root{
    --primary-orange: orange;
}

.container-fluid .navbar-brand{
    font-size: x-large;
    /* letter-spacing: 2px; */
    font-weight: bolder;
}

.navbar-brand > span{
    color: red;
}

.navbar-nav .nav-link{
    font-weight: bolder;
    font-size: 18px;
}

.navbar-nav > .nav-item{
    margin-right: 0.625rem;
    margin-left: 0.625rem;
}

.navbar-nav > .nav-item:hover, .navbar-nav > .nav-item:focus{
    border-top: solid 0.125rem orange;
    border-top: solid 0.125rem var(--primary-orange);
}

nav{
    z-index: 2;
}

body{
    overflow-x: hidden !important;
}

#header-part{
    position: absolute;
    top: 0;
    background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center;
    height: 100vh;
    width: 100vw;
    background-repeat: repeat;
    background-size: cover;
    /* overflow-x: hidden;    */
}

#header-part > .outer-div{
    width: 100vw;
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center !important;
}

.inner-div > p{
    text-align: center;
    color: yellow;
    font-family: 'Fraunces', serif;
    font-size: 70px;
    margin-top: 20px;
}

.styles_typicalWrapper__1_Uvh{
    font-size: 50px;
    font-family: 'Libre Baskerville', serif;
    font-weight: bolder;
    color: white;
}

@media screen and (max-width:1000px){
    .inner-div > p{
        font-size: 37px;
        margin-top: 155px;
    }
    .styles_typicalWrapper__1_Uvh{
        font-size: 30px;
    }
}

.outer-container{
    background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
}
.outer-container3{
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.card{
    position: relative;
    top: 20vh;
    margin-bottom: 10px;
    z-index: 3;
    perspective: 800px;
    animation: skill-style 1.5s ease-in-out 0s;
    background-attachment: fixed;
}

.container > h2{
    position: relative;
    top: 15vh;
}

.card > .card-img{
    height: 200px;
    transform: rotateY(0deg);
    transition: all 0.5s ease-in-out 0s;
    mix-blend-mode: multiply;
    /* perspective: 10px; */
    background: linear-gradient(to right, #c94b4b, #4b134f);
}

.card:hover > .card-img{
    transform: rotateY(90deg);
}

.card-img > img{
    width: 100%;
    height: 100%;
    transform: rotateY(0deg);
    background-color: transparent;
}

.container > h2{
    text-align: center;
    color: yellow;
    font-family: 'Yusei Magic', sans-serif;
    animation: heading 1.5s ease-in-out 0s;
}

.card-content{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, #FC466B, #3F5EFB);
    transform: rotateY(90deg);
    transition: all 0.5s ease-in-out 0s;
}

.card-content > p{
    text-align: center;
    color: yellow;
    font-weight: bolder;
}

.card:hover > .card-content{
    transform: rotateY(0deg);
}

.card  hr{
    width: 90%;
    background-color: white;
}

@media screen and (max-width:1000px){
    .container > h2{
        position: relative;
        top: 40vh;
    }
    .card{
        top: 40vh;
        animation: skill-style-2 1.5s ease-in-out 0s;
    }
    .outer-container3{
        background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center repeat;
        background-size: cover;
        background-attachment: fixed;
        height: 308vh;
    }
}

@keyframes skill-style{
    0%{
        top: 0;
    }
    50%{
        top: 30vh;
    }
    100%{
        top: 20vh;
    }
}

@keyframes heading{
    0%{
        transform: translateX(-50%);
    }
    100%{
        transform: translateX(0);
    }
}

@keyframes skill-style-2{
    0%{
        top: 0;
    }
    100%{
        top: 40vh;
    }   
}

.row > div{
    background-attachment: fixed;
}
.outer-container1{
    height: 147vh;
    width: 100%;
    background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center repeat;
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    top: 0;
    overflow-y: inherit;
}

.inner-container1{
    position: relative;
    top: 15vh;
