@media screen and (max-width: 1000px){
    .inner-container{
        top: 38vh;
    }
}
.outer-container2{
    background: url(/mainportfolio/static/media/joshua-reddekopp-GkFQEOubrCo-unsplash.06c664cf.jpg) center no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    position: absolute;
    background-attachment: fixed;
    top: 0;
}

.inner-container2{
    position: relative;
    top: 15vh;
}
